@import "_lib.scss";

[class*="pgn-row-"] {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: stretch;
	align-content: flex-start;
}

[class*="pgn-col-"] {
	display: inline-block;
	vertical-align: top;
	// flex-grow: 1;
}

[class*="pgn-col-"] > img,
[class*="pgn-col-"] > a > img {
	width: 100%;
}

.pgn-density-relaxed[class*="pgn-row-"],
.pgn-density-relaxed > [class*="pgn-row-"] {
	margin-left: -$padding-relaxed;
	margin-right: -$padding-relaxed;
}
.pgn-density-spaced[class*="pgn-row-"],
.pgn-density-spaced > [class*="pgn-row-"] {
	margin-left: -$padding-spaced;
	margin-right: -$padding-spaced;
}

.pgn-density-relaxed > [class*="pgn-col-"],
.pgn-density-relaxed > [class*="pgn-row-"] > [class*="pgn-col-"] {
	padding: $padding-relaxed;
}
.pgn-density-spaced > [class*="pgn-col-"],
.pgn-density-spaced > [class*="pgn-row-"] > [class*="pgn-col-"] {
	padding: $padding-spaced;
}

@include media-tablet-down {
	.pgn-stacking [class*="pgn-col-"] {
		width: 100%;
	}
}
