@import "all.scss";

h1,
h2,
h3 {
	color: #444;
}

h2 {
	border-bottom: 1px solid #444;
	margin-top: 3rem;
	margin-bottom: 1rem;
	position: relative;
	top: 0;
	left: 0;
}
h2 code {
	color: #666;
	font-size: 80%;
	position: absolute;
	right: 0;
	bottom: 0;
}
h2 code:before {
	content: '@import "';
	color: #aaa;
}
h2 code:after {
	content: '"';
	color: #aaa;
}

h3 {
	margin-top: 2rem;
	margin-bottom: 1rem;
}
h3 code {
	margin-left: 3em;
	font-size: 80%;
	color: #666;
}

.grid-demo [class*="pgn-col-"] > * {
	height: 100%;
	border: 1px solid #333;
}

p code,
dd code {
	background-color: #eee;
}

blockquote {
	margin-left: 3em;
	margin-right: 3em;
}

dt {
	background-color: #eee;
	color: #333;
}

dl {
	display: grid;
	grid-template-columns: max-content auto;
}
dt,
dd {
	padding: 0.25rem;
	margin-bottom: 0.5rem;
}
dt {
	grid-column-start: 1;
	font-weight: bold;
	text-align: right;
}
dd code {
	/* margin-right: 2em; */
}

.html-tag:before {
	content: "<";
}
.html-tag:after {
	content: "/>";
}

.pgn-icon-button {
	font-weight: bold;
}

.box {
	display: block;
	border: 1px solid #333;
}

table#pgn-sizes tbody tr:first-child td {
	font-weight: bold;
	background-color: rgba(127, 127, 127, 0.05);
}
